@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  background: #FFF7F5;
}

/* font-weight */
/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}

@media (max-width: 375px) {
  html {
    font-size: 4.26667vw;
  }
}

@media screen and (max-width: 1319px) {
  html {
    font-size: 1.21212vw;
  }
}

@media screen and (max-width: 767px) {
  html {
    font-size: 16px;
  }
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  font-family: font-family-base;
  font-size: 16px;
  font-weight: 300;
  color: #333;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #FFF7F5;
  overflow-wrap: break-word;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
html,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  max-width: 100%;
  display: block;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  color: inherit;
}

li {
  list-style: none;
}

ul {
  padding-left: 0;
  margin: 0;
}

/* Remove all animations and transitions for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

fieldset,
legend,
button {
  margin: 0;
  padding: 0;
  border: none;
  border-radius: 0;
  outline: none;
  background: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="date"],
input[type="datetime-local"],
input[type="month"],
input[type="time"] {
  margin: 0;
  padding: 4px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 16px;
}

input[type="text"],
input[type="password"],
input[type="email"],
input[type="tel"],
input[type="url"],
input[type="search"],
input[type="number"],
input[type="datetime"],
input[type="week"],
textarea,
select {
  margin-right: 0;
  margin-left: 0;
  padding: 4px;
  border: 1px solid #ccc;
  border-radius: 0;
  outline: none;
  background: none;
  font-size: 16px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

textarea {
  width: 100%;
  height: 100px;
  overflow: auto;
}

select {
  padding-right: 30px;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 12px 12px;
  background-position: right 8px center;
}

.select {
  display: inline-block;
  position: relative;
  overflow: hidden;
  background: #fff;
}

.select select {
  width: 100%;
  padding-right: 24px;
  border: 1px solid #ccc;
  background: none;
  text-overflow: "";
  cursor: pointer;
}

.select::before {
  position: absolute;
  top: 13px;
  right: 8px;
  width: 0;
  height: 0;
  padding: 0;
  content: "";
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid #666;
  pointer-events: none;
}

/* radio & checkbox */
input[type="radio"],
input[type="checkbox"] {
  display: none;
}

input[type="radio"] + span,
input[type="checkbox"] + span {
  display: inline-block;
  position: relative;
  margin-left: 8px;
  padding: 10px 20px;
  color: #555;
  font-size: 1.4rem;
  text-align: center;
  line-height: 1;
  cursor: pointer;
}

input[type="radio"] + span::before,
input[type="checkbox"] + span::before {
  position: absolute;
  top: 50%;
  left: -8px;
  width: 16px;
  height: 16px;
  content: "";
  margin-top: -8px;
  border: 1px solid #ccc;
  background: #fff;
}

/* fieldset */
fieldset {
  padding: 8px 16px;
  border: 1px solid #ccc;
}

legend {
  padding: 0 8px;
}

/* button */
input[type="submit"],
input[type="reset"],
input[type="button"],
button {
  display: inline-block;
  margin: 0;
  padding: 10px 30px;
  border: 1px solid #999;
  border-radius: 0;
  -webkit-box-sizing: content-box;
  -webkit-appearance: button;
  appearance: button;
  background: #efefef;
  color: #000;
  font-size: 20px;
  cursor: pointer;
}

/* ブラウザ対策
---------------------------------------------------------------------------- */
/* Firefox */
/* IE */
select::-ms-expand {
  display: none;
}

/* webkit */
/* iOS */
input[type="submit"]::-webkit-search-decoration,
input[type=reset]::-webkit-search-decoration,
input[type="button"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-decoration {
  display: none;
}

::-webkit-search-cancel-button {
  -webkit-appearance: none;
}

input[type="search"]::-webkit-search-decoration {
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  -moz-appearance: none;
  -webkit-appearance: textfield;
  -webkit-box-sizing: border-box;
  appearance: none;
  background: none;
  outline: none;
  font-size: 16px;
}

/* 挙動
---------------------------------------------------------------------------- */
/* hover */
input:hover,
textarea:hover,
select:hover {
  border-color: #666;
}

input[type="radio"] + span:hover,
input[type="checkbox"] + span:hover {
  color: #000;
}

input[type="radio"] + span:hover::before,
input[type="checkbox"] + span:hover::before {
  border-color: #000;
}

input[type="radio"] + span:hover::after,
input[type="checkbox"] + span:hover::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #ccc;
}

/* checked */
input[type="radio"]:checked + span,
input[type="checkbox"]:checked + span {
  color: #3498db;
}

input[type="radio"]:checked + span::before,
input[type="checkbox"]:checked + span::before {
  border-color: #3498db;
}

input[type="radio"]:checked + span::after,
input[type="checkbox"]:checked + span::after {
  position: absolute;
  content: "";
  top: 50%;
  left: -4px;
  width: 8px;
  height: 8px;
  margin-top: -4px;
  background: #3498db;
}

/* radio */
input[type="radio"] + span::before,
input[type="radio"] + span:hover::after,
input[type="radio"]:checked + span::after {
  border-radius: 50%;
}

/* button */
input[type="submit"]:hover,
input[type="reset"]:hover,
input[type="button"]:hover,
button:hover {
  border-color: #3498db;
  background: #3498db;
  color: #fff;
}

/* focus */
input:focus,
textarea:focus {
  border-color: #3498db;
}

input[type="submit"]:focus,
input[type=reset]:focus,
input[type="button"]:focus,
input[type="search"]:focus {
  outline-offset: -2px;
}

/* disabled */
input:disabled,
input:disabled:hover,
textarea:disabled,
textarea:disabled:hover {
  border-color: #ccc;
  background: #eee;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span,
input[type="checkbox"]:disabled + span {
  color: #ccc;
  cursor: not-allowed;
}

input[type="radio"]:disabled + span::before,
input[type="checkbox"]:disabled + span::before {
  border-color: #ccc;
  cursor: not-allowed;
}

